export const loginFormValidator = (values) => {
  const errors = {}

  if (!values.email) {
    errors.email = ' '
  } else if (!/^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(values.email)) {
    errors.email = '  email '
  }

  if (!values.password) {
    errors.password = ' '
  }

  return errors
}

export const required = value => {
  if (value) return undefined
  return ' '
}

